.dot {
  background: #5345E4;
  -webkit-animation: fx 1300ms ease-in-out;
          animation: fx 1300ms ease-in-out;
  position: absolute;
  /*height: 100vh;*/
  height: 100%;
}

@-webkit-keyframes fx {
  0% {
    width: 0%;
    left:0;
  }
  50% {
    width: 100%;
    left:0;
  }
  100% {
    width: 100%;
    /*left:100%;*/
  }
}

@keyframes fx {
  0% {
    width: 0%;
    left:0;
  }
  50% {
    width: 100%;
    left:0;
  }
  100% {
    width: 100%;
    /*left:100%;*/
  }
}
